<template>
  <main>
    <tab-header />
    <div class="tab-content">
      <div class="tab-pane active show">
        <form class="form">
          <div class="container">
            <div class="row">
              <div class="col-12">
                <div class="radio radio-inline">
                  <input type="radio" v-model="reviewType" value="1">
                  <label for="">調撥對象</label>
                </div>
                <div class="radio radio-inline">
                  <input type="radio" v-model="reviewType" value="2">
                  <label for="">調撥進度</label>
                </div>
                <hr/>
              </div>
              <div class="col-12">
                <h4 class="text-green">選擇撥出對象</h4>
                <div class="form-group row dial-form mb-3" data-index="4"
                :class="{
                  'is-select active': dialOutSelect && dialOutSelect.length
                }">
                  <label for="" class="col-3 text-right">以撥出類別、對象別篩選⬇︎</label>
                  <div class="col-9">
                    <v-select
                    class="taggable"
                    :options="dialOutSelect"
                    v-model="dialOutSelect"
                    :noDrop="true"
                    :disabled="!dialOutSelect || !dialOutSelect.length"
                    label="name"
                    index="id"
                    :taggable="true"
                    :multiple="true">
                    </v-select>
                  </div>
                </div>
              </div>
            </div>
            
            <data-table 
            :prefix="'dial-out'"
            @update="dialOutUpdateSelection"
            :selection="dialOutSelection"
            :options="dialOutTableOption" 
            :table-data="dialOutTableData"
            :remote-data-msg="dialOutRemoteDataMsg"/>
            

            <div class="row">
              <div class="col-12">
                <h4 class="text-green">選擇撥入對象</h4>
                
                <div class="form-group row dial-form mb-3" data-index="6"
                :class="{
                  'is-select active': dialInSelect && dialInSelect.length
                }">
                  <label for="" class="col-3 text-right">以撥入類別、對象別篩選⬇︎</label>
                  <div class="col-9">
                    <v-select
                    class="taggable"
                    :options="dialInSelect"
                    v-model="dialInSelect"
                    :noDrop="true"
                    :disabled="!dialInSelect || !dialInSelect.length"
                    label="name"
                    index="id"
                    taggable
                    multiple>
                    </v-select>
                  </div>
                </div>
              </div>
            </div>
            
            <data-table 
            :prefix="'dial-in'"
            @update="dialInUpdateSelection"
            :selection="dialInSelection"
            :options="dialInTableOption" 
            :table-data="dialInTableData"
            :remote-data-msg="dialInRemoteDataMsg"/>
            
            <div class="col-12 text-right">
              <a @click="next()" class="btn btn-primary">
                下一步
              </a>
            </div>
          </div>
        </form>
      </div>
    </div>
    
    <warning-modal
    v-if="dialInSelect && dialInSelect.length"
    :open="WarningModalOpen"
    :message="'選擇撥出與撥入同仁有重覆，請再次確認!'"
    @submit="confirmChangeDailIn"
    @close="cancelChangeDailIn"
    />
  </main>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import {commonMixin} from '../../mixins/common'
import TabHeader from '../components/TabHeader'
import WarningModal from '../../desktop/components/WarningModal'
import DataTable from "../../components/DataTable/DataTableCheck";
import { debuglog } from 'util';

export default {
  name: 'Index',
  mixins: [commonMixin],
  components: {
    TabHeader, DataTable, WarningModal
  },
  computed: {
    ...mapGetters([
      'News', 'Videos' , 'QueryKeyword'
    ])
  },
  data () {
    return {
      reviewType: 1,
      isSelect: false,
      isSelect2: false,
      dialInSelection: [],
      dialOutSelection: [],
      WarningModalOpen: false,
      dialOutRemoteDataMsg: "資料讀取中，請稍候...",
      dialOutSelection: [],
      dialOutScrollDataLoading: false,
      dialOutTableOption: {
          checkAll: true,
          checkBoxHasUser: false,
          columns: [
              {
                  width: 100,
                  name: "name",
                  label: "服務人員",
                  sorting: true
              },
              {
                  name: "no",
                  label: "收費代號",
                  sorting: true
              },
              {
                  name: "level",
                  label: "職級",
                  sorting: true
              },
              {
                  width: 120,
                  name: "seniority",
                  label: "年資",
                  align: 'right',
                  sorting: true,
                  template: row => {
                    return `${row.seniority}年`
                  }
              },
              {
                  name: "numbers",
                  label: "服務件數(要保人數)",
                  align: 'right',
                  sorting: true
              },
              {
                  name: "capacity",
                  label: "近一年平均產能",
                  align: 'right',
                  sorting: true
              }
          ],
          sorting: (order_by) => {
              this.tableData = []
              if(this.order.order_by == order_by){
                  this.order.order_asc = !this.order.order_asc
              }else{
                  this.order.order_asc = true
              }
              this.order.order_by = order_by
              this.getCustomersAndSetup(this.$store.state.userInfo.id, this.keyowrd)
          }
      },
      dialOutTableData: [
          {
              id: 1,
              name: '尚華強',
              level: '處長',
              seniority: 8,
              no: 'FN59999',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          },
          {
              id: 2,
              name: '吳和龍',
              level: '處長',
              seniority: 8,
              no: 'FN52099',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          },
          {
              id: 3,
              name: '吳孟達',
              level: '處長',
              seniority: 8,
              no: 'FN52501',
              align: 'right',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          }
      ],
      dialInRemoteDataMsg: "資料讀取中，請稍候...",
      dialInSelection: [],
      dialInScrollDataLoading: false,
      dialInTableOption: {
          checkAll: true,
          checkBoxHasUser: false,
          columns: [
              {
                  width: 100,
                  name: "name",
                  label: "服務人員",
                  sorting: true
              },
              {
                  name: "no",
                  label: "收費代號",
                  sorting: true
              },
              {
                  name: "level",
                  label: "職級",
                  sorting: true
              },
              {
                  width: 120,
                  name: "seniority",
                  label: "年資",
                  align: 'right',
                  sorting: true,
                  template: row => {
                    return `${row.seniority}年`
                  }
              },
              {
                  name: "numbers",
                  label: "服務件數(要保人數)",
                  align: 'right',
                  sorting: true
              },
              {
                  name: "capacity",
                  label: "近一年平均產能",
                  align: 'right',
                  sorting: true
              }
          ],
          sorting: (order_by) => {
              this.tableData = []
              if(this.order.order_by == order_by){
                  this.order.order_asc = !this.order.order_asc
              }else{
                  this.order.order_asc = true
              }
              this.order.order_by = order_by
              this.getCustomersAndSetup(this.$store.state.userInfo.id, this.keyowrd)
          }
      },
      dialInTableData: [
          {
              id: 1,
              name: '尚華強',
              level: '處長',
              seniority: 8,
              no: 'FN59999',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          },
          {
              id: 2,
              name: '吳和龍',
              level: '處長',
              seniority: 8,
              no: 'FN52099',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          },
          {
              id: 3,
              name: '吳孟達',
              level: '處長',
              seniority: 8,
              no: 'FN52501',
              align: 'right',
              numbers: '431 (323)',
              capacity: '10Ｐ'
          }
      ],
      dialOutSelect: [],
      dialInSelect: []
    }
  },
  watch: {
    'dialOutSelect': 'dialOutSelectedChange',
    'dialInSelect': 'dialInSelectedChange'
  },
  methods: {
    ...mapActions([
      'loadNews', 'loadVideos' , 'setPageLock' , 'updateQueryKeyword'
    ]),
    dialOutSelectedChange () {
      let newValue = []
      this.dialOutSelect.forEach(data => {
        let item = data.split(' ')
        let select = this.dialOutTableData.filter(row => row.name==item[0] && row.no==item[1])
        if(select.length)
          newValue.push(select[0].id)
      })
      
      this.dialOutSelection = newValue
    },
    dialInSelectedChange () {
      let newValue = []
      this.dialInSelect.forEach(data => {
        let item = data.split(' ')
        let select = this.dialInTableData.filter(row => row.name==item[0] && row.no==item[1])
        if(select.length)
          newValue.push(select[0].id)
      })
      
      this.dialInSelection = newValue
    },
    dialOutUpdateSelection (val) {
      let list = []
      this.dialOutTableData.forEach(data => {
        if (val.indexOf(data.id) > -1) {
          list.push(data.name + ' ' + data.no)
        }
      })
      this.dialOutSelect = list
    },
    dialInUpdateSelection (val) {
      let list = []
      this.dialInTableData.forEach(data => {
        if (val.indexOf(data.id) > -1) {
          list.push(data.name + ' ' + data.no)
        }
      })
      this.dialInSelect = list
    },
    confirmChangeDailIn() {
      this.panelClose('WarningModalOpen')
    },
    cancelChangeDailIn () {
      this.dialInSelect = null
      this.panelClose('WarningModalOpen')
    },
    next () {
      let isSame = false
      this.dialOutSelect.forEach(row => {
        if(this.dialInSelect.indexOf(row) > -1) {
          isSame = true
        }
      })

      if(isSame) {
        this.panelOpen('WarningModalOpen')
      } else {
        this.$router.push('/tab/1/filter')
      }
    }
  },
  beforeMount () {
  },
  mounted () {
  }
}
</script>
