<template>
  <main>
    <tab-header />
    <div class="tab-content">
      <div class="tab-pane active show">
        <div class="container">
            <div class="row">
              <div class="col-12">
                <p>單日上限：2/50</p>
              </div>
            </div>
            
            <div class="row">
                <div class="col-12">
                  <data-table-check
                  :prefix="'cancel-dail'"
                  @update="updateSelection"
                  :table-data="tableData"
                  :options="tableOption"
                  />
                </div>
                <div class="col-12">
                  <a class="btn btn-primary float-right" @click="confirmCheck()" v-if="tableSelection.length">取消調撥</a>
                  <p>
                    註1. 資料更新之系統處理時間為3-5個工作天<br/>
                    註2. 取消調撥期限為每日PM 7:30
                  </p>
                </div>
            </div>

        </div>
      </div>
    </div>
    
    <warning-modal
    :open="cancelCheckOpen"
    :message="'你是否確認要取消調撥？'"
    @submit="cancelCheck"
    @close="panelClose('cancelCheckOpen')"
    />
  </main>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import {commonMixin} from '../../mixins/common'
import DataTableCheck from '../../components/DataTable/DataTableCheck'
import TabHeader from '../components/TabHeader'
import WarningModal from '../../desktop/components/WarningModal'
import { debuglog } from 'util';

const typeData = [
  {
    id: 1,
    charger: 'FN52599/洪金寶',
    newCharger: 'FN52599/洪金寶',
    no: '100345987',
    progress: 0,
    applyAt: '108/03/15'
  },
  {
    id: 2,
    charger: 'FN52599/洪金寶',
    newCharger: 'FN52599/洪金寶',
    no: '100345987',
    progress: 0,
    applyAt: '108/03/15'
  },
  {
    id: 3,
    charger: 'FN52599/洪金寶',
    newCharger: 'FN52599/洪金寶',
    no: '100345987',
    progress: 1,
    applyAt: '108/03/15'
  },
  {
    id: 3,
    charger: 'FN52599/洪金寶',
    newCharger: 'FN52599/洪金寶',
    no: '100345987',
    progress: 2,
    applyAt: '108/03/15'
  },
  {
    id: 3,
    charger: 'FN52599/洪金寶',
    newCharger: 'FN52599/洪金寶',
    no: '100345987',
    progress: -1,
    applyAt: '108/03/15'
  },
]

export default {
  name: 'TransferTab2',
  mixins: [commonMixin],
  components: {
    TabHeader, DataTableCheck, WarningModal
  },
  computed: {
    ...mapGetters([
      'News', 'Videos' , 'QueryKeyword'
    ])
  },
  data () {
    return {
      searchType: 1,
      order: {
          currentTable: 1,
          index: 1,
          asc: true
      },
      cancelCheckOpen: false,
      tableData: typeData,
      tableSelection: [],
      tableOption: {
        checkAll: true,
        columns: [
          {
            name: 'charger',
            label: '原收費代號/收費人員',
            sorting: true
          },
          {
            name: 'newCharger',
            label: '新收費代號/收費人員',
            sorting: true
          },
          {
            name: 'no',
            label: '保單號碼',
            sorting: true
          },
          {
            name: 'progress',
            label: '調撥進度',
            sorting: true,
            template: row => {
              if (row.progress == -1) {
                return `<a class="link text-underline">經理退回</a>`
              }
              if (row.progress == 0) {
                return `<a class="link text-underline">待經理審核</a>`
              }
              if (row.progress == 1) {
                return `<a class="link text-underline">資料更新中</a>`
              }
              if (row.progress == 2) {
                return `<a class="link text-underline">經理已覆核</a>`
              }
            }
          },
          {
            name: 'applyAt',
            sorting: true,
            label: '申請日期'
          }
        ],
        sorting: (index) => {
          // doSorting
          if(this.order.order_by == index){
            this.order.order_asc = !this.order.order_asc
          }else{
            this.order.order_asc = true
          }
          this.order.order_by = index
        }
      }
    }
  },
  watch: {
    'searchType': 'changeType'
  },
  methods: {
    ...mapActions([
      'loadNews', 'loadVideos' , 'setPageLock' , 'updateQueryKeyword'
    ]),
    changeType (val) {
      this.order.currentTable = Number(val)
    },
    search() {
      switch(Number(this.searchType)) {
        case 2:
          this.table2msg = '無任何資料'
          this.type2Data = type2Data
          break;
        case 3:
          this.table3msg = '無任何資料'
          this.type3Data = type3Data
          break;
        case 4:
          this.type4Data = type4Data
          break;
      }
    },
    updateSelection (selection) {
      this.tableSelection = selection
    },
    cancelCheck () {
      this.$toast.info({title: '已取消覆核！'})
      this.panelClose('cancelCheckOpen')
    },
    confirmCheck () {
      this.panelOpen('cancelCheckOpen')
    }
  },
  beforeMount () {
  },
  mounted () {
  }
}
</script>
