import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index.vue'
import TransferTab1Filter from './views/TransferTab1Filter.vue'
import TransferTab2 from './views/TransferTab2.vue'
import TransferTab4 from './views/TransferTab4.vue'
import TransferTab51 from './views/TransferTab51.vue'
import TransferTab52 from './views/TransferTab52.vue'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/1',
      name: 'index',
      component: Index,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/1/filter',
      name: 'transfer-tab-1-filter',
      component: TransferTab1Filter,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/2',
      name: 'transfer-tab-2',
      component: TransferTab2,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/4',
      name: 'transfer-tab-4',
      component: TransferTab4,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/5-1',
      name: 'transfer-tab-5-1',
      component: TransferTab51,
      meta: {requiresAuth: true}
    },
    {
      path: '/tab/5-2',
      name: 'transfer-tab-5-2',
      component: TransferTab52,
      meta: {requiresAuth: true}
    },
    
  ]
})
